@import '../../__data__/style/colors.module.css';
@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';

.header {
    position: fixed;
    background-color: var(--white);
    filter: drop-shadow(5px 5px 5px var(--white-2));
    z-index: 50;
    width: 100%;
    top: 0;
    left: 0;
}

.container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 var(--offset-sm);
}

.headerBody {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100px;
}

@media screen and (min-width: 732px) and (max-width: 1055.98px) {
    .headerBody {
        height: 80px;
    }
}

@media screen and (max-width: 731.98px) {
    .header {
        width: 100%;
        top: 0;
        left: 0;
        z-index: 50;
    }
    .header:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: var(--white);
        z-index: 2;
    }
    .headerBody {
        height: 60px;
    }
}
