@import '../../__data__/style/colors.module.css';
@import '../../__data__/style/sizes.module.css';

.main {
    min-height: 40px;
    border-radius: 3px;
    letter-spacing: 1.05px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    position: relative;
}

.main .spinner {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.blue {
    background-color: var(--blue-6);
    color: var(--white);
}

.blue:hover {
    outline: 1px solid var(--yellow-4);
    box-shadow: 0 0 3pt 2pt var(--yellow-4);
}

.blue:focus {
    outline: 1px solid var(--yellow-4);
    box-shadow: 0 0 3pt 2pt var(--yellow-4);
}

.green {
    background-color: var(--green-4);
    color: var(--white);
}

.green:disabled {
    color: var(--gray-2disabled);
}

.green:hover {
    outline: 1px solid var(--yellow-4);
    box-shadow: 0 0 3pt 2pt var(--yellow-4);
}

.green:focus {
    outline: 1px solid var(--yellow-4);
    box-shadow: 0 0 3pt 2pt var(--yellow-4);
}

.transparent {
    background-color: var(--white);
    color: var(--black-5);
    padding: 5px;
}

.transparent:hover {
    text-decoration: underline;
}

.transparent:focus {
    outline-color: var(--black-5);
}

.lightgreen {
    background-color: var(--green-2);
    color: var(--green-4);
}

.lightgreen:hover {
    outline: 1px solid var(--green-4);
    box-shadow: 0 0 3pt 2pt var(--green-4);
}

.lightgreen:focus {
    outline: 1px solid var(--green-4);
    box-shadow: 0 0 3pt 2pt var(--green-4);
}
