@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/colors.module.css';

.wrapper {
    background: var(--green-2);
    border: 3px solid var(--red-5);
    border-radius: var(--offset-xs);
    padding: var(--offset-xs) var(--offset-md);
    margin: var(--offset-sm) var(--offset-xs);
}

.wrapper .title {
    color: var(--red-5);
}

.wrapper .errorText {
    color: var(--red-5);
}
