@import '../../__data__/style/colors.module.css';
@import '../../__data__/style/sizes.module.css';
@import '../../__data__/style/media.module.css';

.i18ncontainer {
    flex: 1 1 auto;
    text-align: left;
    margin-left: var(--offset-md);
    height: 40px;
    z-index: 3;
}

.i18ncontainer button {
    cursor: pointer;
    background: none;
    border: none;
    color: var(--black);
    font-size: 16px;
}
.i18ncontainer button:first-child {
    margin-right: var(--offset-sm);
}
.i18ncontainer button.active {
    color: var(--green-4);
}
